  const movePrevOrNext = function (billNo, viewmodel, params, beforeAct, afterAct) {
    viewmodel.setCache('deleteError', false);
    // ncc 
    if (!viewmodel.getCache('condition') && process.env.__THEMETYPE__ == 'ncc') return;
    const httpMethod = params.cHttpMethod ? params.cHttpMethod.toLocaleLowerCase() : 'get';
    // get 
    const condition = httpMethod == 'get' ? encodeURI(JSON.stringify(viewmodel.getCache('condition'))) : viewmodel.getCache('condition');

    const postData = {
      billnum: billNo,
      id: viewmodel.get('id').getValue(),
      condition: condition
    }
    // cb.utils.extend(postData, viewmodel.getNecessaryData())
    const beforeActData = {
      params: params,
      data: postData
    }
    beforeAct(beforeActData, function () {
      const callback = function (err, res) {
        if (!err) {
          const bSwitchTpl = _tplIsEquql(res, viewmodel);
          const deleteError = viewmodel.getCache('deleteError')
          if (!deleteError) {
            _reloadVoucher(bSwitchTpl, res, viewmodel, billNo, afterAct)
          }
          // 
          _handlePageInfoStatus(viewmodel, params);
        } else {
          const afterActData = {
            err: err,
            res: res
          }
          if (err && !res) {
            viewmodel.setCache('returnList', true);
          }
          afterAct(afterActData, common.afterAct)
        }
      }
      common.doProxy(params, callback, postData)
    })
  }

