  const add = (billNo, viewmodel, params, beforeAct, afterAct) => {
    if (!viewmodel) {
      return _getInitdata(billNo, params, afterAct);
      // const { cSvcUrl, cHttpMethod } = params.params;
      // const callback = function (err, emptyData) {
      //   if (err) {
      //     common.afterAct({ err });
      //     return;
      //   }
      //   afterAct(null, emptyData);
      // };
      // const postData = Object.assign({ billnum: billNo }, params.carryParams);
      // common.doProxy({ cSvcUrl, cHttpMethod }, callback, postData);
      // return;
    }
    params = params || {};
    if (!params.cAction) {
      const configParams = viewmodel.allActions && viewmodel.allActions.find(item => {
        return item.cAction.trim().toLocaleLowerCase() === 'add';
      });
      Object.assign(params, configParams);
    }
    const queryString = new cb.utils.queryString(params.cSvcUrl)
    let callbackdata = []
    if (queryString.has('code')) {
      const dt = {
        billtype: 'billmaker',
        billno: queryString.get('billno'),
        params: {
          code: queryString.get('code'),
          makeBilltype: queryString.get('makeBilltype')
        }
      }
      cb.loader.runCommandLine('bill', dt, null, function (vm, viewmeta) {
        const callback = function (err, suc) {
          callbackdata = suc
          const afterActData = {
            err: err,
            params: params,
            res: suc
          }
          afterAct(afterActData, common.afterAct)
        }
        const postData = {
          billnum: billNo
        }
        const beforeActData = {
          params: params,
          data: postData
        };
        beforeAct(beforeActData, function () {
          common.emptyBill(postData, params, callback)
          vm.on('afterOkClick', function (args) {
            if (args.data) {
              common.setMode(viewmodel, env.VOUCHER_STATE_ADD)
              const newdata = common.billMerge(args.data, callbackdata)
              render(viewmodel, newdata, true)
            }
          })
          vm.on('close', function () {
            viewmodel.communication({
              type: 'modal',
              payload: false
            })
          })
          viewmodel.communication({
            type: 'modal',
            payload: {
              type: 'platform',
              url: 'billmaker',
              model: viewmodel,
              data: {
                vm: vm,
                viewmeta: viewmeta
              }
            }
          })
        })
      })
    } else {
      let selectedNode;
      const { metaType } = viewmodel.getParams();
      if (metaType === 'treevoucher') {
        const maxLevel = viewmodel.getTreeModel().getState('maxLevel');
        const level = maxLevel === false ? 65536 : (maxLevel || 3);
        selectedNode = _getTreeSelectNode(viewmodel)
        if (selectedNode && selectedNode.level > level) {
          cb.utils.alert(`${level + 1}`, 'error');
          return;
        }
        // common.setMode(viewmodel, env.VOUCHER_STATE_ADD);
      }
      let postData = {
        billnum: billNo
      }
      Object.assign(postData, params.carryParams);
      if (metaType === 'treevoucher' && cb.extendConfig.yyarchive.treeCard) {
        const treeModel = viewmodel.getTreeModel();
        const parentId = treeModel.getState('keyField');
        postData = Object.assign({}, postData, {
          [parentId]: selectedNode && selectedNode[treeModel.getState('keyField')]
        });
      }
      const beforeActData = {
        params: params,
        data: postData
      };
      beforeAct(beforeActData, function () {
        common.emptyBill(postData, params, function (err, suc) {
          const afterActData = {
            err: err,
            params: params,
            res: suc
          }
          if (!err) {
            common.setMode(viewmodel, env.VOUCHER_STATE_ADD);
            viewmodel.addData = suc
            if (viewmodel.get(env.VOUCHER_TPL_KEY)) {
              const currentTpl = viewmodel.get(env.VOUCHER_TPL_KEY).getValue()
              suc[env.VOUCHER_TPL_KEY] = currentTpl
            }
            if (metaType === 'treevoucher') {
              const treeModel = viewmodel.getTreeModel();
              const parentField = treeModel.getState('parentField');
              if (cb.extendConfig.yyarchive.treeCard && suc)
                if (suc[parentField] == null) {
                  if (selectedNode) {
                    suc[parentField] = selectedNode[treeModel.getState('keyField')];
                    suc.parent_name = selectedNode.name;
                  }
                }
            }
            if (cb.extendConfig && cb.extendConfig.theme == 'ncc') {
              render(viewmodel, suc, true);
            } else {
              render(viewmodel, Object.assign({}, suc, params.carryData), true, !params.carryData);
            }
            // render(viewmodel, suc);
            if (params.callback)
              params.callback();
            else {
              const { masterOrgField, masterOrgKeyField } = viewmodel.getParams();
              const masterOrgModel = viewmodel.get(masterOrgField);
              if (masterOrgModel) {
                const model = viewmodel.get(masterOrgKeyField);
                if (model) {
                  const orgId = model.getValue();
                  if (orgId) {
                    const condition = {
                      isExtend: true,
                      simpleVOs: [{ field: 'id', op: 'in', value1: orgId }]
                    };
                    const errCallBack = function () {
                      model.setValue(null);
                      viewmodel.setDisabled(true);
                      viewmodel.get('btnAbandon') && viewmodel.get('btnAbandon').setDisabled(false);
                      masterOrgModel.setDisabled(false);
                      return false;
                    }
                    const vm = masterOrgModel.getCache('vm');
                    if (vm) {
                      vm.execute('pressEnter', { model: masterOrgModel, condition: condition, browse: false, errCallBack });
                    } else {
                      masterOrgModel.browse(false, function (vm) {
                        vm.on('getRefMetaReady', function () {
                          vm.execute('pressEnter', { model: masterOrgModel, condition: condition, browse: false, errCallBack });
                        });
                      });
                    }
                  }
                }
              }
            }
          }
          afterAct(afterActData, common.afterAct)
        })
      })
    }
  }
  const getPredicateValue = formatValue => {
    const exp = /<%.*?%>/g;
    const items = formatValue.match(exp);
    if (!items)
      return formatValue;
    const values = {};
    let valid = true;
    items.forEach(item => {
      const predicate = item.substring(2, item.length - 2).split('.');
      if (predicate.length !== 2) {
        valid = false;
        return;
      }
      const obj = cb.rest.AppContext[predicate[0]];
      values[item] = obj && (cb.utils.isEmpty(obj[predicate[1]]) ? '' : obj[predicate[1]]);
    });
    if (!valid)
      return formatValue;
    return formatValue.replace(exp, function (key) {
      return values[key];
    });
  }
  const localoutput = (billNo, viewmodel, params, beforeAct, afterAct) => {
    const XLSX = require('xlsx');
    const outputData = viewmodel.get(`${params.childrenField}`).getData();
    const dataColumns = viewmodel.get(`${params.childrenField}`).getColumns();
    const res = [];
    const tmpObj = {};
    // 
    for (let i = 0; i < outputData.length; i++) {
      const resObj = {};
      for (const key in dataColumns) {
        const keyName = dataColumns[key].cShowCaption ? dataColumns[key].cShowCaption : dataColumns[key].cCaption;
        if (dataColumns[key].bShowIt && !dataColumns[key].bHidden) {
          const resDetails = outputData[i];
          const resSonObj = {};
          resSonObj.cShowCaption = keyName;
          resSonObj.cControlType = dataColumns[key].cControlType;
          resSonObj.cFormatData = dataColumns[key].cFormatData;
          resSonObj.iNumPoint = dataColumns[key].iNumPoint;
          resSonObj.cEnumString = dataColumns[key].cEnumString;
          let enumValue = null;
          if (dataColumns[key].cEnumString) {
            const enumString = dataColumns[key].cEnumString;
            const lookupVector = Object.keys(eval('(' + enumString + ')')); // eslint-disable-line no-eval
            const resultVector = Object.values(eval('(' + enumString + ')')); // eslint-disable-line no-eval
            const thisLoc = lookupVector.indexOf(resDetails[key].toString());
            enumValue = resultVector[thisLoc];
          }
          resSonObj.iColWidth = dataColumns[key].iColWidth;
          tmpObj[key] = resSonObj;
          resObj[keyName] = enumValue || resDetails[key];
        }
      }
      res.push(resObj);
    }
    // 
    const fileName = `${billNo}.xlsx`;
    const sheet = XLSX.utils.json_to_sheet(res);
    // (header)
    const sheetRowCount = res.length;
    // 
    const colCount = Object.keys(res[0]).length;
    sheet['!cols'] = new Array(colCount);
    const rowRange = XLSX.utils.decode_range(sheet['!ref']);
    for (let R = 0; R <= 1; ++R) {
      for (let C = rowRange.s.c; C <= rowRange.e.c; ++C) {
        if (R == 0) {
          const cell_address = { c: C, r: R };
          /* if an A1-style address is needed, encode the address */
          const cell_ref = XLSX.utils.encode_cell(cell_address);
          const value = sheet[cell_ref].v;
          for (const key in tmpObj) {
            if (tmpObj[key].cShowCaption === value) {
              const controlType = tmpObj[key].cControlType;
              const cellConfig = tmpObj[key];
              let cFormatData = cellConfig.cFormatData;
              sheet['!cols'][C] = { wpx: cellConfig.iColWidth };
              if (cFormatData === 'YYYY-MM-DD') {
                const colRange = { s: { r: 1, c: C }, e: { r: sheetRowCount, c: C } };
                for (let R = colRange.s.r; R <= colRange.e.r; ++R) {
                  const thisCell_address = { c: C, r: R };
                  const thisCell_ref = XLSX.utils.encode_cell(thisCell_address);
                  sheet[thisCell_ref].t = 'd';
                }
              }
              // 
              // let enumString = cellConfig.cEnumString;
              // if (enumString) {
              //   let lookupVector = Object.keys(eval("(" + enumString + ")"));
              //   let resultVector = Object.values(eval("(" + enumString + ")"));
              //   let strLookup = JSON.stringify(lookupVector).replace(/\[(.*)\]/, "$1");
              //   let strResult = JSON.stringify(resultVector).replace(/\[(.*)\]/, "$1");
              //   let colRange = { s: { r: 1, c: C }, e: { r: sheetRowCount, c: C } };
              //   for (let R = colRange.s.r; R <= colRange.e.r; ++R) {
              //     let thisCell_address = { c: C, r: R };
              //     let thisCell_ref = XLSX.utils.encode_cell(thisCell_address);
              //     let cellValue = sheet[thisCell_ref].v;
              //     // let thisLoc = lookupVector.indexOf(cellValue.toString());
              //     // sheet[thisCell_ref].v = resultVector[thisLoc];
              //     let func = `LOOKUP("${cellValue}",{${strLookup}},{${strResult}})`;
              //     sheet[thisCell_ref].f = func;
              //   }

              // }

              switch (controlType && controlType.trim().toLocaleLowerCase()) {
                case 'inputnumber':
                case 'money':
                case 'price': {
                  try {
                    if (!cFormatData || cFormatData == '') {
                      cFormatData = {};
                    } else {
                      cFormatData = JSON.parse(cFormatData);
                    }
                  } catch (e) {
                    cb.utils.alert('//', 'error');
                  }
                  let iNumPoint = cellConfig.iNumPoint;
                  const decimal = cFormatData.decimal ? getPredicateValue(cFormatData.decimal) : null;
                  if (controlType === 'money') {
                    if (decimal)
                      iNumPoint = decimal;
                    else
                      iNumPoint = cb.rest.AppContext.option.amountofdecimal;
                  } else if (controlType === 'price') {
                    if (decimal)
                      iNumPoint = decimal;
                    else
                      iNumPoint = cb.rest.AppContext.option.monovalentdecimal;
                  } else {
                    if (decimal)
                      iNumPoint = decimal;
                    else
                    if (cb.utils.isEmpty(iNumPoint)) iNumPoint = null;
                  }
                  if (controlType === 'money' || (cFormatData.decimal && cFormatData.decimal.indexOf('amountofdecimal'))) {
                    cFormatData.splitter = true
                    // ()
                    if (!iNumPoint) {
                      const colRange = { s: { r: 1, c: C }, e: { r: sheetRowCount, c: C } };
                      for (let R = colRange.s.r; R <= colRange.e.r; ++R) {
                        // eslint-disable-next-line camelcase
                        const thisCell_address = { c: C, r: R };
                        // eslint-disable-next-line camelcase
                        const thisCell_ref = XLSX.utils.encode_cell(thisCell_address);
                        sheet[thisCell_ref].z = XLSX.SSF._table[3];
                      }
                    }
                  }
                  if (iNumPoint) {
                    // ()
                    const colRange = { s: { r: 1, c: C }, e: { r: sheetRowCount, c: C } };
                    for (let R = colRange.s.r; R <= colRange.e.r; ++R) {
                      const thisCell_address = { c: C, r: R };
                      const thisCell_ref = XLSX.utils.encode_cell(thisCell_address);
                      let decimalCount = cFormatData.splitter ? '#,##0.0' : '0.0'
                      for (let i = 1; i < iNumPoint; i++) {
                        decimalCount = decimalCount + '0';
                      }
                      sheet[thisCell_ref].z = decimalCount;
                    }
                  }
                }
              }
            }
          }
        } else if (R === 1) {
          const cell_address = { c: C, r: R };
          /* if an A1-style address is needed, encode the address */
          const cell_ref = XLSX.utils.encode_cell(cell_address);
          if (sheet[cell_ref].t === 'n') {
            let length = sheet[cell_ref].v.toString().length;
            if (sheet[cell_ref].z) {
              const re = /[0-9]+\.([0-9]*)/;
              // 
              const deciLength = sheet[cell_ref].v.toString().replace(re, '$1').length;
              length = length + deciLength;
            } else if (length >= 12) {
              // 
              const colRange = { s: { r: 1, c: C }, e: { r: sheetRowCount, c: C } };
              for (let R = colRange.s.r; R <= colRange.e.r; ++R) {
                const thisCell_address = { c: C, r: R };
                const thisCell_ref = XLSX.utils.encode_cell(thisCell_address);
                sheet[thisCell_ref].z = XLSX.SSF._table[1];
              }
            }
          } else if (sheet[cell_ref].t === 'b') {
            const colRange = { s: { r: 1, c: C }, e: { r: sheetRowCount, c: C } };
            for (let R = colRange.s.r; R <= colRange.e.r; ++R) {
              const thisCell_address = { c: C, r: R };
              const thisCell_ref = XLSX.utils.encode_cell(thisCell_address);
              sheet[thisCell_ref].t = 's';
              sheet[thisCell_ref].v = sheet[thisCell_ref].v ? '' : '';
              // sheet[thisCell_ref].f = `IF(${sheet[thisCell_ref].v},"","")`;
            }
          }
        }
      }
    }
    const wb = XLSX.utils.book_new();
    // wbsheet
    XLSX.utils.book_append_sheet(wb, sheet, `${params.childrenField}`);
    //  Excel
    XLSX.writeFile(wb, fileName);
  }
