  const getYhtUserIds = function (items, callback) {
    if (!items) return;
    if (items.user && items.person) {
      getYhtUserId('user', items.user, function (userIds) {
        getYhtUserId('person', items.person, function (personIds) {
          callback(userIds.concat(personIds));
        })
      })
      return;
    }
    if (items.user)
      getYhtUserId('user', items.user, callback);
    if (items.person || items.id)
      getYhtUserId('person', items.person || items.id, callback);
  }

  const getYhtUserId = (type, models, callback) => {
    let refCode = ''; let yhtField = '';
    switch (type) {
      case 'user':
        refCode = 'productcenter.aa_user';
        yhtField = 'yhtUserId';
        break;
      case 'person':
      case 'id':
        refCode = 'ucf-staff-center.bd_staff_ref';
        yhtField = 'userId';
        break;
    }
    if (!refCode) return;
    const proxy = cb.rest.DynamicProxy.create({
      getYhtUserId: { url: '/bill/ref/getRefData', method: 'POST' }
    });
    const ids = [];
    models.forEach(model => {
      const id = model.getValue();
      if (!id) return;
      ids.push(id);
    })
    const params = {
      refCode,
      dataType: 'grid',
      condition: {
        isExtend: true,
        simpleVOs: [{
          field: 'id',
          op: 'in',
          value1: ids
        }]
      }
    }
    proxy.getYhtUserId(params, (_err, result) => {
      if (result && result.recordList && result.recordList.length) {
        const yhtIds = [];
        result.recordList.forEach(item => {
          yhtIds.push(item[yhtField]);
        });
        callback(yhtIds);
      }
    });
  }

