  const importVoucher = (billNo, viewModel, params, beforeAct, afterAct) => {
    const btnCallBack = {
      disabledCallback: params.disabledCallback,
      enabledCallback: params.enabledCallback
    }
    params.disabledCallback = false;
    params.enabledCallback = false;
    const beforeActData = { params, data: {} };
    let isAsync = false;
    let asyncInterval = 1;
    const asyncKey = 'asyncImport_' + cb.utils.getNewGuid().replace(/-/g, '');
    let acceptedFileType = ''
    if (params.cParameter) {
      const cParameter = JSON.parse(params.cParameter);
      if (cParameter) {
        isAsync = cParameter.isAsync;
        asyncInterval = cParameter.interval || asyncInterval;
        acceptedFileType = cParameter.acceptedFileType || ''
      }
    }
    // isAsync = true;
    beforeAct(beforeActData, function () {
      const eleInput = document.createElement('input');
      eleInput.type = 'file';
      if (acceptedFileType) {
        eleInput.accept = acceptedFileType // 
      }
      eleInput.style.display = 'none';
      eleInput.onchange = function (e) {
        btnCallBack.disabledCallback && btnCallBack.disabledCallback();
        const formData = new FormData();
        formData.enctype = 'multipart/form-data';
        const file = e.target.files[0];
        const fileName = file && file.name;
        const fileSize = file && file.size;
        formData.append('file', file);
        if (beforeActData.data.mapCondition)
          formData.append('mapCondition', JSON.stringify(beforeActData.data.mapCondition));
        const xhr = new XMLHttpRequest();
        let url = `${params.cSvcUrl}?billnum=${common.getVmParams(viewModel, 'cardKey')}`;
        if (isAsync) {
          url = url + '&asyncKey=' + asyncKey;
        }
        const _url = window._baseUrl ? `${window._baseUrl}${url}` : url;
        xhr.open('POST', _url, true);
        xhr.send(formData);
        if (isAsync) {
          btnCallBack.enabledCallback && btnCallBack.enabledCallback();
          xhr.onreadystatechange = function () {
            let bError = false;
            let errorInfo = '';
            if (xhr.readyState !== 4) return;
            if (xhr.status === 200) {
              let ajaxResult;
              try {
                ajaxResult = JSON.parse(xhr.responseText);
              }
              catch (e) {
                bError = true;
                errorInfo = ',' + xhr.responseText;
              }
              if (ajaxResult.code === 200) {
              } else {
                bError = true;
                errorInfo = `${ajaxResult.message}`;
              }
            }
            else {
              bError = true;
              errorInfo = ' xhr.readyState =' + xhr.readyState + '  xhr.status =' + xhr.status + '  xhr.responseText = ' + xhr.responseText;
            }
            if (bError) {
              // let _timer = viewModel.getCache(asyncKey);
              // viewModel.clearCache(asyncKey);
              // _timer && clearTimeout(_timer);
              cb.utils.alert(errorInfo);
            }
            else {
              const curTime = cb.utils.getNowFormatDate();
              const busName = viewModel.getParams() && (viewModel.getParams().name || viewModel.getParams().caption);
              const asyncParams = { asyncKey, fileSize, fileName, curTime, busName, percentage: 0, asyncData: '{}' };
              viewModel.communication({
                type: 'asyncImport',
                payload: asyncParams
              })
              const _timer = setInterval(() => {
                asyncImportQuery(viewModel, asyncParams, btnCallBack);
              }, asyncInterval * 1000);
              viewModel.setCache(asyncKey, _timer);
              cb.utils.alert('');
            }
          }
        }
        else {
          xhr.onreadystatechange = function () {
            if (xhr.readyState !== 4) return;
            btnCallBack.enabledCallback && btnCallBack.enabledCallback();
            if (xhr.status === 200) {
              let ajaxResult;
              try {
                ajaxResult = JSON.parse(xhr.responseText);
              }
              catch (e) {
                cb.utils.alert(',' + xhr.responseText);
                return;
              }
              if (ajaxResult.code === 200) {
                const res = ajaxResult.data;
                if (res.sucessCount) {
                  viewModel.execute('refresh');
                  viewModel.execute('afterImport', res);
                }

                viewModel.communication({
                  type: 'modal',
                  payload: {
                    key: 'BatchMsg',
                    data: {
                      type: 2,
                      res
                    }
                  }
                });
              } else {
                cb.utils.alert(`${ajaxResult.message}`, 'error');
              }
            }
            else {
              console.log(' xhr.readyState =' + xhr.readyState + '  xhr.status =' + xhr.status + '  xhr.responseText = ' + xhr.responseText);
            }
          };
        }
        document.body.removeChild(eleInput);
      }
      document.body.appendChild(eleInput);
      eleInput.click();
    });
  }

  const asyncImportQuery = (viewModel, asyncParams, btnCallBack) => {
    const asyncKey = asyncParams.asyncKey;
    const url = 'billtemp/getImportProcess?';
    const proxy = cb.rest.DynamicProxy.create({
      getImportProcess: {
        url: url,
        method: 'GET',
        options: { mask: false }
      }
    })

    proxy.getImportProcess({ asyncKey: asyncKey }, function (err, result) {
      let errStr = '';
      if (err) {
        errStr = err.message;
        handleAsyncImportOver(viewModel, asyncParams, errStr);
        return;
      }
      if (result) {
        let res;
        try {
          res = JSON.parse(result);
        }
        catch (e) {
          errStr = ',' + result;
          handleAsyncImportOver(viewModel, asyncParams, errStr);
          return;
        }
        const percentage = parseFloat(res.percentage || '0');
        if (res.flag == '0') {
          errStr = '';
          if (res.data)
            errStr = errStr + ',' + res.data;
          handleAsyncImportOver(viewModel, asyncParams, errStr);
        }
        else if (res.flag == 1 && percentage >= 100) {
          handleAsyncImportOver(viewModel, asyncParams);
          viewModel.execute('refresh');
          viewModel.execute('afterImport', res);
          viewModel.execute('openNotification', Object.assign({ res: res.data }, asyncParams));
        }
        asyncParams.asyncData = result;
        asyncParams.percentage = percentage;
        viewModel.communication({
          type: 'asyncImport',
          payload: asyncParams
        });
      }
    });
  }

  const handleAsyncImportOver = (viewModel, asyncParams, errStr) => {
    const _timer = viewModel.getCache(asyncParams.asyncKey);
    viewModel.clearCache(asyncParams.asyncKey);
    _timer && clearTimeout(_timer);
    if (errStr) {
      cb.utils.alert(errStr);
      asyncParams.bDelTask = true;
      asyncParams.errStr = errStr;
      viewModel.communication({
        type: 'asyncImport',
        payload: asyncParams
      });
    }
  }
